.profile__name {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 450px;
}
/* —á—Ç–æ–±—ã –Ω–µ –ª–æ–º–∞–ª—Å—è –∞–≤–∞—Ç–∞—Ä –æ—Ç –¥–ª–∏–Ω–Ω–æ–≥–æ —Ç–µ–∫—Å—Ç–∞ */
@media screen and (max-width: 1023px) {
  .profile__name {
    max-width: 350px;
  }
}

@media screen and (max-width: 768px) {
  .profile__name {
    text-align: center;
  }
}

@media screen and (max-width: 425px) {
  .profile__name {
    font-size: 27px;
    line-height: 1.22;
  }
}