.elements__list {
  margin: 0;
  padding: 0;
  display: grid;
  row-gap: 20px;
  column-gap: 18px;
  justify-items: center;
  list-style: none;
}

/* 290px = 282px —à–∏—Ä–∏–Ω—ã –∫–∞—Ä—Ç–æ—á–∫–∏ + –Ω–µ–±–æ–ª—å—à–∞—è –æ—Ö—Ä–∞–Ω–Ω–∞—è –∑–æ–Ω–∞ */
@media (min-width: 290px) {
  .elements__list {
    grid-template-columns: repeat(1, 1fr);
  }
}

/* —à–∏—Ä–∏–Ω–∞ = (282px —à–∏—Ä–∏–Ω—ã –∫–∞—Ä—Ç–æ—á–∫–∏ + gap) * 2 –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ —Ä—è–¥ + (–Ω–µ–±–æ–ª—å—à–∞—è –æ—Ö—Ä–∞–Ω–Ω–∞—è –∑–æ–Ω–∞ —Å –¥–≤—É—Ö —Å—Ç–æ—Ä–æ–Ω) */
@media (min-width: calc((282px + 17px) * 2 + (5px * 2))) {
  .elements__list {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* —à–∏—Ä–∏–Ω–∞ = (282px —à–∏—Ä–∏–Ω—ã –∫–∞—Ä—Ç–æ—á–∫–∏ + gap) * 3 –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ —Ä—è–¥ + (–Ω–µ–±–æ–ª—å—à–∞—è –æ—Ö—Ä–∞–Ω–Ω–∞—è –∑–æ–Ω–∞ —Å –¥–≤—É—Ö —Å—Ç–æ—Ä–æ–Ω) */
@media (min-width: calc((282px + 17px) * 3 + (5px * 2))) {
  .elements__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

