.profile__description {
  margin: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.22;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 550px;
}
/* —á—Ç–æ–±—ã –Ω–µ –ª–æ–º–∞–ª—Å—è –∞–≤–∞—Ç–∞—Ä –æ—Ç –¥–ª–∏–Ω–Ω–æ–≥–æ —Ç–µ–∫—Å—Ç–∞ */
@media screen and (max-width: 1023px) {
  .profile__description {
    max-width: 400px;
  }
}

@media screen and (max-width: 768px) {
  .profile__description {
    text-align: center;
  }
}

@media screen and (max-width: 425px) {
  .profile__description {
    font-size: 14px;
    line-height: 1.21;
  }
}